import React, { Component, ReactElement } from "react";
import * as axios from "axios";

// Template components
import { LinksBlock } from "./template/LinksBlock";
import { Text } from "./template/Text";
import { ImageBlock } from "./template/ImageBlock";
import { CodeBlock } from "./template/CodeBlock";
import { Title } from "./template/Title";

abstract class AbstractPage extends Component {
  public props: object;

  public constructor(props: object) {
    super(props);
    this.props = props;
  }

  protected setPage = (pageId: number): void => {
    axios["get"](
      `https://sha1om.github.io/dooboox_api/pages/page-${pageId}/index.json`
    ).then((response: object) => {
      if (response["data"]) {
        this.props["actionCreator"]("SET_CURRENT_PAGE", response["data"][0]);
      }
    });
  };

  protected setPageSearcherValue = (newValue: number | "") => {
    this.props["actionCreator"]("SET_PAGE_SEARCHER_VALUE", newValue);
  };
}

abstract class PageEngine extends AbstractPage {
  public constructor(props: object) {
    super(props);
  }

  protected createPage = (pageItems: Array<object>): Array<object> => {
    let page: Array<object> = pageItems.map((item: object): object => {
      return this.createElement(item);
    });

    return page;
  };

  protected createElement = (pageElement: object) => {
    switch (pageElement["name"]) {
      case "title": {
        return <Title value={pageElement["value"]} />;
      }

      case "imageBlock": {
        return (
          <ImageBlock
            image={pageElement["items"].image}
            id={pageElement["items"].id}
            sublinks={[...pageElement["items"].sublinks]}
          />
        );
      }

      case "codeBox": {
        return (
          <CodeBlock
            lines={[...pageElement["items"].codeLines]}
            result={[...pageElement["items"].resultLines]}
            highlight={this.props["syntaxHighlight"].highlight}
          />
        );
      }

      case "text": {
        if (Object.keys(this.props["texts"]).length === 0) {
          axios["get"](pageElement["source"]).then((response: object) => {
            this.props["actionCreator"]("ADD_NEW_TEXT", {
              name: pageElement["id"],
              text: response["data"],
            });
          });
        }

        return (
          <Text
            value={[this.props["texts"][pageElement["id"]]]}
            id={pageElement["id"]}
          />
        );
      }

      case "links": {
        return <LinksBlock links={[...pageElement["items"]]} />;
      }

      default: {
        return {};
      }
    }
  };

  protected createPagination = (pageId: number, nextPage: boolean) => {
    let previous,
      next: ReactElement | string = "";

    if (!(pageId < 2)) {
      previous = (
        <div
          className="tp-pagination_button previous-button"
          onClick={() => {
            this.setPage(pageId - 1);
          }}
        >
          <a href="#">
            <span>Previous</span>
          </a>
        </div>
      );
    }

    if (nextPage) {
      next = (
        <div
          className="tp-pagination_button next-button"
          onClick={() => {
            this.setPage(pageId + 1);
          }}
        >
          <a href="#">
            <span>Next</span>
          </a>
        </div>
      );
    }

    return (
      <div className="tp-pagination_block">
        {previous}
        {next}
        <div className="tp-page-searcher">
          <input
            type="text"
            id="pageSearcher"
            placeholder="Page number"
            value={this.props["pageSearcherValue"]}
            onChange={(e) =>
              this.setPageSearcherValue(Number(e.target.value) || "")
            }
          />
          <div
            className="tp-page-searcher_button"
            onClick={() => this.setPage(this.props["pageSearcherValue"])}
          >
            <a href="#">
              <span>Search</span>
            </a>
          </div>
        </div>
      </div>
    );
  };
}

export abstract class Page extends PageEngine {
  public constructor(props: object) {
    super(props);
  }

  protected getInitalPage = () => {
    if (this.props["currentPage"].length === 0) {
      this.setPage(1);
    }
  };
}
